.frc-tabs-only {
  font-weight: $font-weight-normal;
  color: $words-normal;
  text-align: center;
  box-sizing: border-box;
  display: table-cell;
  border-radius: $border-radius;
  position: relative;
  zoom: 1;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  &::after {
    display: block;
    content: "";
    clear: both;
  }
  .frc-tab-pane {
    height: 100%;
    float: left;
    box-sizing: border-box;
    padding: 0;
    background-color: $tabs-base-background-transparent;
    transition: all 0.3s ease-in-out;
    position: relative;
    &:hover {
      background-color: $tabs-base-background-hover;
      cursor: pointer;
    }
    .frc-tab-pane-content {
      min-width: 80px;
      height: 100%;
      padding: 0 $tabs-base-padding;
      box-sizing: border-box;
      @include nowrap;
    }
  }
}
