.frc-tabs-only.frc-tabs-only-piend {
  background: $tabs-base-background-transparent;
  border: 1px solid $tabs-base-background-transparent;

  .frc-tab-pane {
    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: -2px;
      display: block;
      width: 100%;
      height: 2px;
      border-radius: 0;
      background-image: none;
    }
  }

  .active {
    background: $basic-background;
    &:hover {
      background-color: $basic-background;
    }
    &::after {
      background-image: linear-gradient(
        270deg,
        $high-light-area-top 0%,
        $high-light-area-right 100%
      );
    }
  }
}
.frc-tabs-only.frc-tabs-only-disabled {
  cursor: not-allowed;
  color: $words-disable;
  .frc-tab-pane {
    color: $words-disable;
    cursor: not-allowed;
    &:hover {
      background-color: $tabs-base-background-transparent;
      cursor: not-allowed;
    }
  }
  .frc-tab-pane.active {
    &:hover {
      background-color: $tabs-base-border-color-background;
    }
  }
  .active {
    background: $tabs-base-border-color-background;
    color: $words-disable;
  }
}
