.frc-tabs-only-size-default {
  @include tabs-size(
    $tabs-item-default-height,
    $tabs-item-default-line-height,
    $font-size-base
  );
}

.frc-tabs-only-size-middle {
  @include tabs-size(
    $tabs-item-middle-height,
    $tabs-item-middle-line-height,
    $font-size-base
  );
}
.frc-tabs-only-size-large {
  @include tabs-size(
    $tabs-item-large-height,
    $tabs-item-large-line-height,
    $font-size-medium
  );
}

.frc-tabs-only-not-atuo-width {
  .frc-tab-pane {
    .frc-tab-pane-content {
      width: 80px;
      @include nowrap;
    }
